import React, {useState} from 'react';
import {observer} from "mobx-react-lite";
import './Login.css';
import {Page} from "../../components";
import {RouteComponentProps} from "react-router";
import {IonSpinner, useIonViewWillEnter} from "@ionic/react";
import {useStores} from "../../models/root-store/root-store-context";
import {useApiToaster} from "../../helpers-ts";


export const RecuperarContrasena: React.FC<RouteComponentProps> = observer(() => {
  const rootStore = useStores()
  const [Email, setEmail] = useState('')
  const [Loading, setLoading] = useState(false)
  const [EmailValido, setEmailValido] = useState(false)
  const [Movil, setMovil] = useState(false)
  const apiToast = useApiToaster()

  const enviarCorreoRecuperacion = () => {
    // eslint-disable-next-line no-restricted-globals
    if (!confirm('Desea enviar un email a ' + Email + ' para cambiar su contraseña?')) {
      return false
    }
    rootStore.environment.api.recuperarContrasena(Email).then((result: any) => {
      setLoading(false)
      apiToast(result, 'Se envió un enlace para recuperar su contraseña a su correo', 'enviar correo')
      setEmailValido(false)
      setEmail('')
    })
  }

  const validarEmail = (email: string) => {
    const reg = /^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/
    if (reg.test(email)) {
      setEmailValido(true)
    } else {
      setEmailValido(false)
    }
  }

  useIonViewWillEnter(async () => {
    setMovil((window.innerWidth <= 500))
  })

  return <Page login_required={false} hide_padding_margin_top_movil>

    <div className={"page-wrapper text-center d-flex"}>
      <div className={"background-white d-flex flex-column justify-content-center align-items-center w-100"} style={{minHeight: !Movil ? '46vh' : '100vh'}}>
        <div className={'row w-100'}>
          <div className={'col-12 col-md-4 mx-auto'}>
            <div className={'card my-5'}>
              <div className={'card-body text-left'}>
                <h4>Ingresá tu email para poder recuperar tu cuenta</h4>
                <div className={'form-group'}>
                  <label>Email</label>
                  <input
                    type={'email'}
                    className={'form-control'}
                    value={Email}
                    onChange={event => {
                      validarEmail(event.target.value)
                      setEmail(event.target.value)
                    }}
                    autoCapitalize={'none'}
                  />
                </div>
                <div className={'form-group'}>
                  <button
                    className={'btn btn-primary btn-block'}
                    disabled={Loading || Email === '' || !EmailValido}
                    onClick={enviarCorreoRecuperacion}
                    style={{minHeight: 42}}
                  >
                    {Loading && <IonSpinner name="crescent"/>}
                    {!Loading && <span style={{fontSize: 15}}>Enviar email de  recuperación</span>}
                  </button>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </Page>;
});

export default RecuperarContrasena;
